import React, { useEffect, useState } from 'react'
import { Game } from './Game';
import Swal from 'sweetalert2';

let palabras = [];

export const Option = () => {

    const [option, setOption] = useState('');
    const [showContent, setShowContent] = useState(true);

    const handleClickButtonFacil = () => {
        setOption("facil");
        setShowContent(false);
        palabras = ["Elefante", "Lana", "Kira", "Coli", "León", "Mastodonte", "Delfín", "Perro", "Gato", "Cocodrilo", "Caracol", "Avestruz", "Tigre", "Pelícano", "Colibrí", "Foca", "Tucán", "Oso", "Lince", "Caballo", "Cabra", "Cebra", "Castor", "Mapache", "Koala", "Burro", "Avispa", "Águila", "Cisne", "Cobaya", "Búfalo", "Reno", "Gallina", "Escorpión", "Hormiga", "Halcón", "Gorila", "Gacela", "Jabalí", "Jirafa", "Mamut", "Oveja", "Pulga", "Puma", "Zarigüeya", "Pingüino", "Pulpo"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 6; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    const handleClickButtonNormal = () => {
        setOption("normal");
        setShowContent(false);
        palabras = ["Elefante", "Lana", "Kira", "Coli", "León", "Mastodonte", "Delfín", "Perro", "Gato", "Cocodrilo", "Caracol", "Avestruz", "Tigre", "Pelícano", "Colibrí", "Foca", "Tucán", "Oso", "Lince", "Caballo", "Cabra", "Cebra", "Castor", "Mapache", "Koala", "Burro", "Avispa", "Águila", "Cisne", "Cobaya", "Búfalo", "Reno", "Gallina", "Escorpión", "Hormiga", "Halcón", "Gorila", "Gacela", "Jabalí", "Jirafa", "Mamut", "Oveja", "Pulga", "Puma", "Zarigüeya", "Pingüino", "Pulpo"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 9; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    const handleClickButtonDificil = () => {
        setOption("dificil");
        setShowContent(false);
        palabras = ["Cartón", "Caracol", "Casa", "Caramba", "Canción", "Campeón", "Campo", "Calipso", "Calvo", "Cabeza", "Cactus", "Cabra", "Cacatúa", "Caballo", "Caballero", "Cacahuete", "Casino", "Cable", "Cabaña"];
        shuffle(palabras);
        let palabrasTemp = [];
        for (let index = 0; index < 9; index++) {
            palabrasTemp.push(palabras[index]);
        }
        palabras = palabrasTemp;
    }

    const handleClickButtonResetPoints = () => {

        Swal.fire({
            title: '¿Reiniciar estadísticas?',
            text: "No podrás recuperarlas.",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: 'Cancelar',
            confirmButtonText: 'Reiniciar estadísticas'
        }).then((result) => {
            if (result.isConfirmed) {
                Swal.fire(
                    '¡Borradas!',
                    'Tus estadísticas se han reiniciado.',
                    'success'
                ).then(() => {
                    localStorage.clear();
                    window.location.reload();
                })
            }
        })
    }

    function shuffle(array) {
        var currentIndex = array.length, temporaryValue, randomIndex;

        while (0 !== currentIndex) {

            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }

        return array;
    }

    let partidasFacilesJugadas = localStorage.getItem("partidasFacilesJugadas");
    let partidasFacilesGanadas = localStorage.getItem("partidasFacilesGanadas");

    const [porcentajePartidasFaciles, setPorcentajePartidasFaciles] = useState(0);

    let partidasNormalesJugadas = localStorage.getItem("partidasNormalesJugadas");
    let partidasNormalesGanadas = localStorage.getItem("partidasNormalesGanadas");

    const [porcentajePartidasNormales, setPorcentajePartidasNormales] = useState(0);

    let partidasDificilesJugadas = localStorage.getItem("partidasDificilesJugadas");
    let partidasDificilesGanadas = localStorage.getItem("partidasDificilesGanadas");

    const [porcentajePartidasDificiles, setPorcentajePartidasDificiles] = useState(0);

    useEffect(() => {
        if (!partidasFacilesGanadas) {
            setPorcentajePartidasFaciles(0);
        } else {
            setPorcentajePartidasFaciles((((partidasFacilesGanadas / partidasFacilesJugadas) * 100)).toFixed(2));
        }

        if (!partidasNormalesGanadas) {
            setPorcentajePartidasNormales(0);
        } else {
            setPorcentajePartidasNormales((((partidasNormalesGanadas / partidasNormalesJugadas) * 100)).toFixed(2));
        }

        if (!partidasDificilesGanadas) {
            setPorcentajePartidasDificiles(0);
        } else {
            setPorcentajePartidasDificiles((((partidasDificilesGanadas / partidasDificilesJugadas) * 100)).toFixed(2));
        }

    }, [])

    return (
        <>
            {option === "facil" ? (
                <Game palabras={palabras} option={option} />
            ) : null}
            { option === "normal" ? (
                <Game palabras={palabras} />
            ) : null}
            { option === "dificil" ? (
                <Game palabras={palabras} option={option} />
            ) : null}
            {
                showContent ? (
                    <div>
                        <div className="container mb-5">
                            <h1 className="text-center mt-2 title_memory_game">Memory Game</h1>
                            <h6 className="text-center"><i>Creado por Christianinsky</i></h6>
                            <h6 className="text-center mt-4">Elige una opción</h6>
                            <div className="container">
                                <div className="row mt-3">
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonFacil} className="btn btn-success">Fácil</button>
                                    </div>
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonNormal} className="btn btn-info">Normal</button>
                                    </div>
                                    <div className="col-4 m-auto text-center">
                                        <button onClick={handleClickButtonDificil} className="btn btn-danger">Difícil</button>
                                    </div>
                                </div>
                            </div>
                            <div className="container mt-5 card pt-3 bg-aqua">
                                <h5>Tu puntuación:</h5>
                                <div>
                                    <p>Partidas fáciles jugadas: {localStorage.getItem("partidasFacilesJugadas") === null ? 0 : (
                                        <>
                                            {partidasFacilesJugadas}
                                        </>
                                    )}</p>
                                    <p>Partidas fáciles ganadas: {localStorage.getItem("partidasFacilesGanadas") === null ? 0 : (
                                        <>
                                            {partidasFacilesGanadas}
                                        </>
                                    )}</p>
                                    <p>{porcentajePartidasFaciles ? (<span>Porcentaje de victorias: {porcentajePartidasFaciles}%</span>) : (<span>Porcentaje de victorias: 0%</span>)}</p>
                                </div>
                                <hr />
                                <div>
                                    <p>Partidas normales jugadas: {localStorage.getItem("partidasNormalesJugadas") === null ? 0 : (
                                        <>
                                            {partidasNormalesJugadas}
                                        </>
                                    )}</p>
                                    <p>Partidas normales ganadas: {localStorage.getItem("partidasNormalesGanadas") === null ? 0 : (
                                        <>
                                            {partidasNormalesGanadas}
                                        </>
                                    )}</p>
                                    <p>{porcentajePartidasNormales ? (<span>Porcentaje de victorias: {porcentajePartidasNormales}%</span>) : <span>Porcentaje de victorias: 0%</span>}</p>
                                </div>
                                <hr />
                                <div>
                                    <p>Partidas difíciles jugadas: {localStorage.getItem("partidasDificilesJugadas") === null ? 0 : (
                                        <>
                                            {partidasDificilesJugadas}
                                        </>
                                    )}</p>
                                    <p>Partidas difíciles ganadas: {localStorage.getItem("partidasDificilesGanadas") === null ? 0 : (
                                        <>
                                            {partidasDificilesGanadas}
                                        </>
                                    )}</p>
                                    <p>{porcentajePartidasDificiles ? (<span>Porcentaje de victorias: {porcentajePartidasDificiles}%</span>) : (<span>Porcentaje de victorias: 0%</span>)}</p>
                                </div>
                                <hr />
                                <div className="mb-3">
                                    <button onClick={handleClickButtonResetPoints} className="btn btn-dark">Reiniciar estadísticas</button>
                                </div>
                            </div>
                        </div>
                    </div>
                ) : null
            }
        </>
    )
}
